@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
  font-size: 62.5%;

  //  
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked;
  }

  //   ,    open-only-mob
  &.css-scroll-fixed.open-only-mob {
    @include media("<tab") {
      @include scroll-blocked;
    }
  }
}

body {
  min-width: $layout-page-min-width;

  @include font-normal;
  @include font-size($font-size, $line-height);

  color: $text-color;
  background: $bg-color url(../../img/empty.png) no-repeat left top fixed;

  @include media('<tab') {
    font-size: $font-size--tab;
    line-height: $line-height--tab;
  }

  @include media('<mob') {
    font-size: $font-size--mob;
    line-height: $line-height--mob;
  }
}

//    
//       
@if nth($layout-page-space, 2) != 0 {
  .layout-page-spaces {
    @include padding-horizontal(nth($layout-page-space, 2));

    @include media('<lap-l') {
      @include padding-horizontal(nth($layout-page-space--net, 2));
    }

    @include media('<tab') {
      @include padding-horizontal(nth($layout-page-space--tab, 2));
    }

    @include media('<mob') {
      @include padding-horizontal(nth($layout-page-space--mob, 2));
    }

    .has-sidebar & {
      padding-left: $sidebar-width;

      @include media('<desk') {
        padding-left: $sidebar-width--lap;
      }

      @include media('<lap-l') {
        padding-left: $sidebar-width--net;
      }

      @include media('<tab') {
        padding-left: nth($layout-page-space--tab, 2);
      }

      @include media('<mob') {
        padding-left: nth($layout-page-space--mob, 2);
      }
    }
  }
}

//      
//       
.layout-page {
  max-width: $layout-page-max-width;
  //box-sizing: content-box;

  //@include push-auto;
}

//     
@if nth($layout-text-space, 2) != 0 {
  .layout-text-spaces {
    @include padding-horizontal(nth($layout-text-space, 2));
  }
}

//       
.layout-text {
  max-width: $layout-text-width;
  box-sizing: content-box;

  @include push-auto;
}

//      
.layout-flood {
  max-width: $layout-text-width;
  box-sizing: content-box;

  @include push-auto;
}

//     
//  html, body    100% (  reset.sass)
body {
  display: flex;
  flex-direction: column;
}

.header {
  flex-shrink: 0;
}

.main {
  flex-shrink: 0;
}

.footer {
  flex-shrink: 0;
  margin-top: auto;
}