  border-radius: 4px;
}

.charity-form-box .form-fields,
.charity-form-box .form-row {
  margin-bottom: 3rem;
}

.charity-form-box .form-fields:last-child,
.charity-form-box .form-row:last-child {
  margin-bottom: 0;
}

.charity-form-box .form-unit:last-child {
  margin-bottom: 0;
}

.charity-form-btn {
  display: block;
  width: 100%;
  height: 7.4rem;
  min-width: 14rem;
  text-align: center;
  padding: 0 4rem;
  font-family: inherit;
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 7.4rem;
  text-decoration: none;
  color: #fff;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  background: #0095cb;
  background: -webkit-linear-gradient(-90deg, #0095cb -52.74%, #005c9c 100%);
  background: linear-gradient(180deg, #0095cb -52.74%, #005c9c 100%);
}

@media (max-width: 639px) {
  .charity-form-btn {
    height: 5.2rem;
    font-size: 1.6rem;
    line-height: 5.2rem;
  }
}

.no-touchevents .charity-form-btn:hover {
  background: #0095cb;
  background: -webkit-linear-gradient(-90deg, #0095cb 0%, #054c9c 100%);
  background: linear-gradient(180deg, #0095cb 0%, #054c9c 100%);
}

